import movieBox from '../../components/index/movieBox/movieBox.vue'
import trailerBox from '../../components/index/trailerBox/trailerBox.vue'
import uLikeBox from '../../components/index/uLikeBox/uLikeBox.vue'
export default {
	data() {
		return {
			carouselList: {},
			hotList: {},
			trailerList: null,
			uLikeList: {},
		}
	},
	components: {
		movieBox,
		trailerBox,
		uLikeBox
	},
	methods: {
		getCarousel() {
			uni.showLoading({
				title: "加载中..."
			})
			uni.request({
				url: this.$api("/index/carousel/list"),
				method: 'POST',
				success: (res) => {
					this.carouselList = res.data.data
				},
				fail() {

				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		getHotList(type) {
			uni.showLoading({
				title: "加载中..."
			})
			uni.request({
				url: this.$api("index/movie/hot"),
				method: 'POST',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data: {
					type //trailer
				},
				success: (res) => {
					type === 'trailer' ? this.trailerList = res.data.data : this.hotList = res.data.data
				},
				fail() {

				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		getULike() {
			uni.showLoading({
				title: "加载中..."
			})
			uni.request({
				url: this.$api("index/guessULike"),
				method: 'POST',
				success: (res) => {
					this.uLikeList = res.data.data
				},
				fail() {

				},
				complete() {
					uni.hideLoading()
				}
			})
		},
		gotoDetail(id) {
			uni.navigateTo({
				url: `/pages/detail/detail?id=${id}`
			})
		}
	},
	onLoad() {
		this.getCarousel();
		this.getHotList('trailer');
		this.getHotList('superhero');
		this.getULike();
	},
	onPullDownRefresh() {
		this.getULike();
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 200)

	}
}
